<script>
import Slider from "../home/components/SliderArea.vue"
import { ref, onMounted, watch } from "vue"
import Footer from "../../components/global/footer/FooterArea.vue"
import { useRouter } from "vue-router"
import { apiGetCourseList } from "@/api/api"

export default {
  setup() {
    const router = useRouter()
    let courseData = ref([])

    onMounted(async () => {
      let APIparams = ref({ page: 1, limit: 5 })
      const response = await apiGetCourseList(APIparams.value)
      courseData.value = response.data.list
    })

    let mode = process.env.VUE_APP_MODE
    const goto = (val) => {
      router.push({ path: val })
    }
    return { goto, mode, courseData }
  },
  components: { Slider },
}
</script>

<template>
  <div class="courseIndex">
    <!-- 表頭 -->
    <header class="c-header c-header-2b">
      <nav class="navbar ui-navbar">
        <ul class="navbar-nav">
          <li class="nav-item">
            <a @click="goto('/home')" class="nav-link"><i class="far fa-chevron-left"></i></a>
          </li>
        </ul>
        <h1 class="navbar-brand">課程專區</h1>
        <ul class="navbar-nav">
          <li class="nav-item">
            <a href="javascript:void(0);" class="nav-link"></a>
          </li>
        </ul>
      </nav>
    </header>

    <!-- 內容 -->
    <section class="c-main c-main-2">
      <div class="index-block minHeight-outer">
        <Slider />
        <div class="banner-container">
          <a href="javascript:void(0);" v-if="mode == 'purple'"><img src="@/assets/image/banner_01.png" /></a>
          <a href="javascript:void(0);" v-if="mode == 'pink'"><img src="@/assets/image/banner_02.png" /></a>
        </div>
        <div class="menu-outer-4">
          <div class="row">
            <div class="col-4">
              <a @click="goto('/course/newCourse')" class="menu-link">
                <div class="menu-img"><i class="fas fa-laptop"></i></div>
                <div class="menu-title">課程清單</div>
              </a>
            </div>
            <div class="col-4">
              <a @click="goto('/course/myCourse')" class="menu-link">
                <div class="menu-img"><i class="fas fa-shield-alt"></i></div>
                <div class="menu-title">我的課程</div>
              </a>
            </div>
            <div class="col-4">
              <a @click="goto('/course/faq')" class="menu-link">
                <div class="menu-img"><i class="fas fa-question"></i></div>
                <div class="menu-title">聯繫我們</div>
              </a>
            </div>
          </div>
        </div>
        <!--  -->
        <div>
          <div class="row mt-2 mb-5">
            <div>
              <span class="title2 ms-2">最新課程</span>
              <div class="card" v-for="item in courseData" :key="item.id">
                <div class="card-body">
                  <a @click="goto(`/course/info/${item.courseId}`)" class="card-link">
                    <div class="card-title custom-ellipsis">
                      {{ item.name }}
                    </div>
                    <div class="card-date">
                      課程日期 : {{ item.startDate.slice(0, 10) }} ~
                      {{ item.endDate.slice(0, 10) }}
                    </div>
                    <div class="card-organize">
                      培訓單位 : {{ item.organizer }}
                    </div>
                    <div class="card-hour">訓練時數 : {{ item.hours }}</div>
                  </a>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <!--  -->
    </section>
  </div>
</template>

<style lang="scss" scoped>
@import "@/assets/custom/config";


.courseIndex {
  height: calc(100vh - $footer-height);
  background-color: #f7f7f7 ;
}
.index-block{
  height: calc(100vh - $footer-height);
}

.custom-ellipsis {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.title2 {
  font-size: 16px;
  font-weight: 400;
  color: nth($primary-color, 2);
}
</style>
